<template>
  <div class="fenlei" ref="fenlei">
  <div class="left">
    <div v-for="(item,index) in navList" class="fenlei-item" :class="{'selected':curNav == index}">
      <p @click="selectNav(item.id,index)">{{item.name}}</p>
    </div>
  </div>
  <div class="right">
    <Product2 :product2="product"></Product2>
  </div>
</div>
</template>

<script>
import axios from "axios"
import qs from 'qs'
import Product2 from "./Product2"

export default {
  name: 'fenlei',
  // props: 'fenlei',
  components: {
    Product2
  },
  data () {
    return {
      product:[],
      productTest:[{ "cp_id" : 635, "cp_mingcheng" : "CHANDO++", "jiage" : "25", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan18.jpg" } , { "cp_id" : 634, "cp_mingcheng" : " 21   ", "jiage" : "2", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan17.jpg" } , { "cp_id" : 633, "cp_mingcheng" : " +", "jiage" : "70", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan16.jpg" } , { "cp_id" : 632, "cp_mingcheng" : "(NIVEA)150g( )", "jiage" : "80", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan15.jpg" } , { "cp_id" : 631, "cp_mingcheng" : "(NIVEA)50g  ", "jiage" : "35", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan14.jpg" } , { "cp_id" : 630, "cp_mingcheng" : "CHANDO++", "jiage" : "25", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan13.jpg" } , { "cp_id" : 629, "cp_mingcheng" : " 21   ", "jiage" : "15", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan12.jpg" } , { "cp_id" : 628, "cp_mingcheng" : " +", "jiage" : "20", "cp_tupian" : "http://www.yaoyiwangluo.com/upload/jingxuan11.jpg" }],
      navList:[],
      navListTest:[{ "id" : 387, "name": "" } , { "id" : 350, "name": "" } , { "id" : 442, "name": "" } , { "id" : 443, "name": "" } , { "id" : 444, "name": "" } , { "id" : 349, "name": "" } , { "id" : 348, "name": "" }],
      curNav: 0
    }
  },
  methods:{
    selectNav(id,index) {
      this.curNav = index;
      axios.post('/api/wx_fenlei_chanpin.asp',
        qs.stringify({int_lxid1: id})
      ).then(res=>{
        this.product = res.data;
        console.log("request success3");
        console.log("product="+res);
        console.log("id="+id)
      }).catch(error=>{
        this.product = this.productTest
        console.log("request fail3")
      })
    },
    getNavList() {
      axios.get('/api/wx_fenlei.asp').then(res=>{
        this.navList = res.data;
        console.log("request success1")
      }).catch(error=>{
        this.navList = this.navListTest
        console.log("request fail1")
      })
    },
    getProuctLists() {
      axios.get('/api/wx_CpList_top4.asp').then(res=>{
        this.product = res.data;
        console.log("request success2")
      }).catch(error=>{
        this.product = this.productTest
        console.log("request fail2")
      })
    },
    getWinHeight() {
      const that = this;
      window.screenHeight = document.documentElement.clientHeight;
      that.$refs.fenlei.style.height = (window.screenHeight - 75) + "px";
      window.onresize = () => {
          window.screenHeight = document.documentElement.clientHeight;
          // that.screenWidth = window.screenWidth
          that.$refs.fenlei.style.height = (window.screenHeight - 75) + "px";
          console.log("height"+window.screenHeight)
      }
    }
  },
  mounted(){
    this.getNavList();
    this.getProuctLists();
    this.getWinHeight();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p {
  display: block;
  margin: 0;
}
.fenlei {
  display: flex;
  width: 100%;
  height: 100%;
}
.left { 
  width: 20%;
  /*height: 100%;*/
  background-color: #eee;
  font-size: .85em;
  /*margin-bottom: 75px;*/
  overflow: scroll;
}
.fenlei-item {
  display: inline-block;
  width: 100%;
  text-align: center;
  padding: 5px 0;
  border-bottom: 1px solid #ddd;
}
.selected{
  margin-right: -1px;
  padding-left:-1px;
  color: #333;
  background-color: #fff;
}
.right {
  width: 80%;
  /*margin-bottom: 75px;*/
  overflow: scroll;
}
</style>



